.image__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 20px;
  margin-top: 50px;
  margin-bottom: 50px;
}

.image__list img {
  width: 250px;
  height: 250px;
  object-fit: cover;
  box-shadow: -1px -2px 10px 0px rgba(50, 50, 50, 0.74);
}
